        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>有禁用选项</h3>
<div class="demo-block">
    <div class="demo-select">
        <in-select v-model="value3">
            <in-option value="上海" label="上海"></in-option>
            <in-option value="北京" label="北京"></in-option>
            <in-option value="黄山" label="黄山"></in-option>
            <in-option disabled value="杭州" label="杭州"></in-option>
        
    </in-select></div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-select v-model="value3">
    <in-option value="上海" label="上海"></in-option>
    <in-option value="北京" label="北京"></in-option>
    <in-option value="黄山" label="黄山"></in-option>
    <in-option disabled value="杭州" label="杭州"></in-option>


</in-select></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">in-select</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"上海"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"上海"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-option</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"北京"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"北京"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-option</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-option</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"黄山"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"黄山"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-option</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">in-option</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"杭州"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"杭州"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-option</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">in-selet</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value3</span>:<span class="hljs-string">'杭州'</span>
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>in-select 组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>booblean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>value(v-model)</td>
<td>选择框的值</td>
<td>string/number</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>in-option 组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>booblean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>选项的值</td>
<td>string/number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>lable</td>
<td>选项显示的标签值</td>
<td>string/number</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>组件事件</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>参数回调</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>组件值改变时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.demo-select{
    width:200px;
}
</style>
<script>
export default {
    data(){
        return {
            value1:'上海',
            value2:'黄山',
            value3:'杭州'
        }
    }
}
</script>


