<template><section>
<h1>table 案例</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
    <in-table :data="list">
        <template slot-scope="scope">
            <in-table-column label="#">
                {{scope.index+1}}
            </in-table-column>
            <in-table-column label="昵称">
                {{scope.row.nickName}}
            </in-table-column>
            <in-table-column label="平台">
                {{scope.row.plat}}
            </in-table-column>
            <in-table-column label="邀请码">
                {{scope.row.code}}
            </in-table-column>
            <in-table-column label="操作" width="200">
                <in-button type="text">通过</in-button>
                <in-button type="text">拒绝</in-button>
            </in-table-column>
        </template>
    </in-table>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-table :data="list">
    <template slot-scope="scope">
        <in-table-column label="#">
            {{scope.index+1}}
        </in-table-column>
        <in-table-column label="昵称">
            {{scope.row.nickName}}
        </in-table-column>
        <in-table-column label="平台">
            {{scope.row.plat}}
        </in-table-column>
        <in-table-column label="邀请码">
            {{scope.row.code}}
        </in-table-column>
        <in-table-column label="操作" width="200">
            <in-button type="text">通过</in-button>
            <in-button type="text">拒绝</in-button>
        </in-table-column>
    </template>
</in-table>
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">in-table</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"list"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"scope"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-table-column</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"#"</span>&gt;</span>
            {{scope.index+1}}
        <span class="hljs-tag">&lt;/<span class="hljs-name">in-table-column</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-table-column</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"昵称"</span>&gt;</span>
            {{scope.row.nickName}}
        <span class="hljs-tag">&lt;/<span class="hljs-name">in-table-column</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-table-column</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"平台"</span>&gt;</span>
            {{scope.row.plat}}
        <span class="hljs-tag">&lt;/<span class="hljs-name">in-table-column</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-table-column</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"邀请码"</span>&gt;</span>
            {{scope.row.code}}
        <span class="hljs-tag">&lt;/<span class="hljs-name">in-table-column</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">in-table-column</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"操作"</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"200"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">in-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>&gt;</span>通过<span class="hljs-tag">&lt;/<span class="hljs-name">in-button</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">in-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>&gt;</span>拒绝<span class="hljs-tag">&lt;/<span class="hljs-name">in-button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">in-table-column</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">in-table</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>in-table 属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>表格数据</td>
<td>array</td>
<td>[]</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>template 属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>slot-scope</td>
<td>通过slot-scope 可以取值到row 和 index</td>
<td>object</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>in-table-column 属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>列标题</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
           list:[{
                nickName:'就废话特别多',
                plat:'微博',
                code:'INGVIP0',
                status:'待审核'
            },{
                nickName:'就废话特别多0123123',
                plat:'小红书',
                code:'INGVIP30',
                status:'待发货'
            }]
        }
    }
};
</script>


